
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'ShowObject',
    props: {
        name: String,
        value: [Object, Array, String, Number, Boolean, Function],
    },
    data() {
        return {
        };
    },
    computed: {
        innerId(): string {
            const value = Math.random() * Math.pow(2, 32);
            return value.toString();
        },
        isPrimitive(): boolean {
            const type = typeof this.value;

            switch (type) {
                case 'string':
                case 'number':
                case 'boolean':
                case 'function':
                case 'undefined':
                    return true;
                case 'object':
                    if (type === null) {
                        return true;
                    }
                    /* falls through */
                default:
                    return false;
            }
        },
        stringValue(): string {
            return String(this.value);
        },
        innerProps(): string[] {
            const value = this.value;
            if (!value) {
                return [];
            }
            return Array.from(Object.keys(value as unknown as Array<string>));
        },
    },
});
